/**
 * Script para crear usuarios demo
 * Ejecutar desde consola del navegador
 */

export const createDemoUsers = async () => {
  try {
    console.log('üöÄ Iniciando creaci√≥n de datos demo...');
    
    // Credenciales demo
    const demoAccounts = [
      {
        email: 'admin@sysscholar.com',
        password: 'Admin123!',
        userData: {
          nombre: 'Administrador Principal',
          usuario: 'admin_sys',
          edad: 35,
          cedula: '1234567890',
          celular: '0991234567',
          a√±o: '1',
          rol: 'admin',
          fechaRegistro: serverTimestamp(),
          penalizado: false,
          motivoPenalizacion: '',
          fotoURL: 'https://ui-avatars.com/api/?name=Admin+Sys&background=30297A&color=fff',
          ultimoAcceso: serverTimestamp(),
          progreso: {
            a√±o1: { completado: true, nivelesCompletados: 5, totalNiveles: 5 },
            a√±o2: { completado: true, nivelesCompletados: 6, totalNiveles: 6 }
          }
        }
      },
      {
        email: 'estudiante@sysscholar.com',
        password: 'Estudiante123!',
        userData: {
          nombre: 'Juan P√©rez Demo',
          usuario: 'juan_perez',
          edad: 22,
          cedula: '0987654321',
          celular: '0999876543',
          a√±o: '1',
          rol: 'estudiante',
          fechaRegistro: serverTimestamp(),
          penalizado: false,
          motivoPenalizacion: '',
          fotoURL: 'https://ui-avatars.com/api/?name=Juan+Perez&background=2563eb&color=fff',
          ultimoAcceso: serverTimestamp(),
          progreso: {
            a√±o1: { completado: false, nivelesCompletados: 2, totalNiveles: 5 },
            a√±o2: { completado: false, nivelesCompletados: 0, totalNiveles: 6 }
          }
        }
      }
    ];

    const results = [];

    for (const account of demoAccounts) {
      try {
        console.log(`üìß Creando: ${account.email}`);
        
        // Intentar crear usuario
        const userCredential = await createUserWithEmailAndPassword(
          auth,
          account.email,
          account.password
        );
        
        const userId = userCredential.user.uid;
        
        // Guardar datos en Firestore
        await setDoc(doc(db, 'users', userId), {
          ...account.userData,
          uid: userId,
          email: account.email
        });
        
        // Cerrar sesi√≥n despu√©s de crear
        await signOut(auth);
        
        results.push({ email: account.email, success: true, message: '‚úÖ Creado exitosamente' });
        console.log(`‚úÖ ${account.email} creado`);
        
      } catch (error) {
        if (error.code === 'auth/email-already-in-use') {
          console.log(`‚ö†Ô∏è ${account.email} ya existe, actualizando datos...`);
          
          try {
            // Iniciar sesi√≥n para actualizar
            const loginCredential = await signInWithEmailAndPassword(
              auth,
              account.email,
              account.password
            );
            
            const userId = loginCredential.user.uid;
            
            // Actualizar datos en Firestore
            await setDoc(doc(db, 'users', userId), {
              ...account.userData,
              uid: userId,
              email: account.email
            }, { merge: true });
            
            await signOut(auth);
            results.push({ email: account.email, success: true, message: '‚úÖ Actualizado exitosamente' });
            console.log(`‚úÖ ${account.email} actualizado`);
            
          } catch (loginError) {
            results.push({ email: account.email, success: false, message: `‚ùå Error: ${loginError.message}` });
            console.error(`‚ùå Error con ${account.email}:`, loginError.message);
          }
        } else {
          results.push({ email: account.email, success: false, message: `‚ùå Error: ${error.message}` });
          console.error(`‚ùå Error creando ${account.email}:`, error.message);
        }
      }
    }

    return results;

  } catch (error) {
    console.error('‚ùå Error general:', error);
    return [{ success: false, message: `‚ùå Error general: ${error.message}` }];
  }
};

